import logo from './logo.svg';
import './App.css';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import {useState,useEffect} from 'react';
import firebase from './firebase';

function App() {

const [firstname,setfirstname]=useState();
const [lastname,setlastname]=useState();

const [ufirstname,setufirstname]=useState();
const [ulastname,setulastname]=useState();
const [userid,setuserid]=useState();


const [userdata,setuserdata]=useState([]);

useEffect(() =>{
const firestore=firebase.database().ref('/userinfo');
firestore.on('value',(response) =>{
  const data=response.val();
  let userinfo=[];

  for(let id in data){
    userinfo.push({
      id:id,
      FirstName:data[id].FirstName,
      LastName:data[id].LastName
    });
  }
  setuserdata(userinfo);
});
},[]);

const handleAddUser = (e) => {
e.preventDefault();
const firestore = firebase.database().ref('/userinfo');

firestore.push({
  FirstName:firstname,
  LastName:lastname,
  });

  alert("form submitted");

};

const handleUpdateClick = (data) =>{
 
  setufirstname(data.FirstName);
  setulastname(data.LastName);
  setuserid(data.id);
}


const handleUpdateUser = () =>{

  const firestore =firebase.database().ref('/userinfo').child(userid);
  firestore.update({
    FirstName:ufirstname,
    LastName:ulastname,
  })

  alert("data updated")
}


const handleDeleteUser = (id) =>{

  const firestore =firebase.database().ref('/userinfo').child(id);

  firestore.remove();
}

  

return (
    <>

<div class="background1">
<h1 class="myheading">CRUD operations in firebase.</h1>

<form onSubmit={handleAddUser}>
  <div class="mb-3">
    <label for="exampleInputEmail1" class="form-label">first name</label>
    <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" value={firstname} onChange={e => setfirstname(e.target.value)}/>
    
  </div>
  <div class="mb-3">
    <label for="exampleInputPassword1" class="form-label">last name</label>
    <input type="text" class="form-control" id="exampleInputPassword1" value={lastname} onChange={e => setlastname(e.target.value)}/>
  </div>
  
  <button type="submit" class="btn btn-primary" >Submit</button>
</form>
<br></br>
<br />
Update Data
<hr></hr>
<br />
<br />

<form onSubmit={() => {handleUpdateUser()}}>
  <div class="mb-3">
    <label for="exampleInputEmail1" class="form-label">ufirst name</label>
    <input type="text" class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" value={ufirstname} onChange={e => setufirstname(e.target.value)}/>
    
  </div>
  <div class="mb-3">
    <label for="exampleInputPassword1" class="form-label">ulast name</label>
    <input type="text" class="form-control" id="exampleInputPassword1" value={ulastname} onChange={e => setulastname(e.target.value)}/>
  </div>
  
  <button type="submit" class="btn btn-primary" >Submit</button>
</form>







<table class="table">
  <thead>
    <tr>
    
      <th scope="col">First</th>
      <th scope="col">Last</th>
    
      
    </tr>
  </thead>
  <tbody>


{

userdata.map((data,index) =>{

return(

  <tr>
      
      <td>{data.FirstName}</td>
      <td>{data.LastName}</td>
     <td><button type="button" class="btn btn-primary" onClick={() => {handleUpdateClick(data)}}>update</button> <button type="button" class="btn btn-danger" onClick={() => {handleDeleteUser(data.id)}}>delete</button></td>
    
    </tr>
   


);
})

}


    
  </tbody>
</table>


</div>


    </>
  );
}

export default App;
